@import url(./__logo/header__logo.css);

.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
  margin: 0 auto 0 auto;
  box-sizing: border-box;
  padding: 45px 0 41px 0;
  margin-bottom: 36px;
  position: relative;
}

header::before {
  bottom: 0;
  left: 0;
  position: absolute;
  content: '';
  width: 100%;
  height: 1px;
  background-color: #545454;
}

@media (max-width: 600px) {
  .header {
    padding-left: 0;
    padding-right: 0;
  }

  header::before {
    width: 100%;
    left: 0;
  }
}

@media (max-width: 400px) {
  .header {
    padding-top: 28px;
    padding-bottom: 32px;
    margin-bottom: 38px;
  }
}
