@import url(./__container/popup__container.css);
@import url(./__form/popup__form.css);
@import url(./__save-button/popup__save-button.css);
@import url(./__title/popup__title.css);
@import url(./__close-icon/popup__close-icon.css);
@import url(./__capture/popup__capture.css);
@import url(./__image/popup__image.css);
@import url(./__container-image/popup__container-image.css);
@import url(../popup/__close-icon/_image/popup__close-icon_image.css);

.popup {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 2;
  display: flex;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0s, opacity 0.3s;
  justify-content: center;
  align-items: center;
}
