.popup__form-input {
  width: 100%;
  height: 27px;
  border: none;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
  padding-left: 5px;
}

.popup__form-input:focus {
  outline-color: #000000;
}

.popup__form-input:last-of-type {
  margin-top: 40px;
}
