.profile__author {
  margin: 0;
  font-style: normal;
  font-weight: 500;
  font-size: 42px;
  line-height: 56px;
  margin-bottom: 14px;
  margin-right: 17px;
  max-width: 400px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  grid-area: name;
}

@media (max-width: 965px) {
  .profile__author {
    font-weight: 500;
    font-size: 27px;
    max-width: 300px;
  }
}

@media (max-width: 600px) {
  .profile__author {
    margin-bottom: 0;
    margin-right: 10px;
  }
}
