.popup__save-button {
  width: 100%;
  height: 50px;
  font-weight: normal;
  font-size: 18px;
  line-height: 22px;
  text-align: center;
  color: #ffffff;
  margin: 0;
  border: none;
  background: #000000;
  border-radius: 2px;
  cursor: pointer;
  transition: background 0.3s;
  margin-top: 48px;
}

.popup__save-button:hover {
  background: rgba(0, 0, 0, 0.8);
}

.popup__save-button:focus {
  outline: none;
}

.popup__save-button:disabled {
  background: #ffffff;
  border: 2px solid #000000;
  color: #000000;
  opacity: 0.3;
  cursor: default;
}

.popup__save-button:disabled:hover {
  background: #ffffff;
  border: 2px solid #000000;
  color: #000000;
  opacity: 0.3;
  cursor: default;
}

@media (max-width: 768px) {
  .popup__save-button:hover {
    transition: none;
    background: rgba(0, 0, 0, 1);
  }
}
