.profile__description {
  margin: 0;
  font-weight: normal;
  font-size: 18px;
  line-height: 22px;
  white-space: nowrap;
  max-width: 400px;
  text-overflow: ellipsis;
  overflow: hidden;
  grid-area: description;
}

@media (max-width: 768px) {
  .profile__description {
    font-size: 14px;
    line-height: 17px;
  }
}

@media (max-width: 800px) {
  .profile__description {
    text-align: center;
    margin-bottom: 36px;
  }
}
