.element__like {
  cursor: pointer;
  width: 21px;
  height: 19px;
  border: none;
  background-image: url(../../../images/like.svg);
  background-size: cover;
  background-position: center center;
  background-color: unset;
  transition: opacity 0.3s;
}

.element__like:hover {
  opacity: 0.6;
}

.element__like:focus {
  outline: none;
}
