.popup__title {
  font-weight: 900;
  font-size: 24px;
  line-height: 29px;
  color: #000000;
  margin: 0;
  margin-bottom: 54px;
}

@media (max-width: 640px) {
  .popup__title {
    font-size: 20px;
  }
}

@media (max-width: 430px) {
  .popup__title {
    font-size: 16px;
    margin-bottom: 30px;
  }
}
