.profile__avatar-overlay {
  width: 120px;
  height: 120px;
  margin-right: 30px;
  border-radius: 50%;
  position: relative;
  cursor: pointer;
  z-index: 1;
}

.profile__avatar-overlay:hover {
  background: rgba(0, 0, 0, 0.8);
  background-image: url(../../../images/change.svg);
  background-position: center center;
  background-repeat: no-repeat;
}
